import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import logo from './sunLogo.png';
import schoolLogo from './schoolLogo.png';
import backspace from './backspace.png';
import './App.css';
import 'semantic-ui-css/semantic.min.css';
import { Progress , Button } from 'semantic-ui-react';
import ChargingInfo from './ChargingInfo';

function App() {
  const [ch_power, setChPower] = useState(null); // ch_power = DB에서 가져온 데이터

  const total_power = 5000;
  let percent_charged = (ch_power / total_power) * 100;

  const navigate = useNavigate();

  const goBack = () => {
    navigate(-1);
  }

  useEffect(() => {
    // 데이터를 가져오는 함수
    const fetchData = async () => {
        try {
            const response = await fetch('https://gss-hanium.com/getData');
            const result = await response.json();

            console.log("Server Response:", result);

                if (result && result.length > 0) {
                  // 데이터를 timestamp 기준으로 정렬하고 가장 최근 항목의 energy 값을 가져옵니다.
                  const latestData = result.sort((a, b) => b.timestamp - a.timestamp)[0];
                  setChPower(latestData.energy);
              }
        } catch (error) {
            console.error("Error fetching data:", error);
        }
    };

    fetchData();

    // 주기적으로 데이터를 가져오기 위한 인터벌 설정
    const intervalId = setInterval(fetchData, 1000); // 1sec

    // 컴포넌트 언마운트 시 인터벌 제거
    return () => clearInterval(intervalId);
}, []);  // useEffect에 빈 의존성 배열을 전달하여 컴포넌트가 마운트될 때 한 번만 실행

  return (
    <div className='App'>
      <div className="green-nav">
       <h1>Green Solar System</h1>
      </div>
      <div style={{ display: 'flex', justifyContent: 'left' }}>
        <button className="backspace" onClick={goBack}>
          <img src={ backspace } alt="Go back" style={{ width: '20px', height: '20px' }}/>
        </button>
      </div>
      <div>
        <h1 style={{ marginBottom: '20px' }}></h1>
      </div>
        <img src={ logo } className='App-logo' alt='logo' />
      <div>
        <h1 style={{ marginBottom: '20px' }}></h1>
      </div>
      <ChargingInfo ch_power = {ch_power} />
      <Link to="/power-stats">
        <Button primary>발전량 통계</Button>
      </Link>
      <div>
        <h1 style={{ marginBottom: '1px' }}></h1>
      </div>
      <Link to="/manage-devices">
        <Button primary>기기 관리</Button>
      </Link>
      <div>
        <h1 style={{ marginBottom: '20px' }}></h1>
      </div>
      <img src={schoolLogo} className='school-logo' alt='school logo' />
    </div>
  );
}

export default App;
