// ChargingInfo.js
import React from 'react';
import { Progress } from 'semantic-ui-react';

const ChargingInfo = React.memo(({ ch_power }) => {
    const total_power = 5000;
    const percent_charged = (ch_power / total_power) * 100;

    return (
        <>
            <h4>충전량 : { ch_power } mAh</h4>
            <div className='percent-bar'>
                <Progress percent={ percent_charged } indicating style = {{ width: '50%' }}/>
            </div>
        </>
    );
});

export default ChargingInfo;
