import React , { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import backspace from './backspace.png';
import schoolLogo from './schoolLogo.png';
import logo from './sunLogo.png';
import { Button } from 'semantic-ui-react';
import { Doughnut } from 'react-chartjs-2';
import { Chart, ArcElement, CategoryScale, DoughnutController } from 'chart.js';

Chart.register(ArcElement, CategoryScale, DoughnutController);

function ManageDevices() {
  const navigate = useNavigate();

  const goBack = () => {
    navigate(-1);
  }

  const [daysPassed, setDaysPassed] = useState(27); // 청소한지 몇일이 지났는지 추적하는 상태 변수

  const cleaningData = {
    labels: ['지난 날짜', '남은 날짜'],
    datasets: [
      {
        data: [daysPassed, 30 - daysPassed],
        backgroundColor: daysPassed < 20 ? ['green', '#e0e0e0'] : daysPassed < 25 ? ['yellow', '#e0e0e0'] : ['red', '#e0e0e0'],
      },
    ],
  };

  const handleCleaningDone = () => {
    setDaysPassed(0); //상태 초기화
  };

  return (
    <div className='App'>
      <div className="green-nav">
        <h1>Green Solar System</h1>
      </div>
      <div style={{ display: 'flex', justifyContent: 'left' }}>
        <button className="backspace" onClick={goBack}>
          <img src={ backspace } alt="Go back" style={{ width: '20px', height: '20px' }}/>
        </button>
      </div>
      <div>
        <h1 style={{ marginBottom: '20px' }}></h1>
      </div>
      <Link to="/" style={{ display: 'inline-block' }}> 
        <img src={ logo } className='App-logo' alt='logo' />
      </Link>
      <div>
        <h1 style={{ marginBottom: '20px' }}></h1>
      </div>
      <h3>기기 관리</h3>
      <div>
        <h1 style={{marginBottom: '15px'}}></h1>
      </div>
      <h3>청소 주기</h3>
      <div style={{ width: '50%', margin: '0 auto'}}>
        <Doughnut data={cleaningData} />
      </div>
      <div>
        <h1 style={{marginBottom: '10px'}}></h1>
      </div>
      <Button onClick={handleCleaningDone}>청소 완료</Button>
      <div>
        <h1 style={{marginBottom: '15px'}}></h1>
      </div>
      <Link to="/device-info">
        <Button primary>기기 정보</Button>
      </Link>
      <div>
        <h1 style={{ marginBottom: '1px' }}></h1>
      </div>
      <Link to="/register-device">
        <Button primary>기기 등록</Button>
      </Link>
      <div>
        <h1 style={{ marginBottom: '50px' }}></h1>
      </div>
      <div className='gray-font'>
        <h4>로고 이미지를 클릭하면 홈 화면으로 이동합니다.</h4>
      </div>
      <div>
        <h1 style={{ marginBottom: '20px' }}></h1>
      </div>
      <img src={schoolLogo} className='school-logo' alt='school logo' />
    </div>
  );
}

export default ManageDevices;



