import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import backspace from './backspace.png';
import schoolLogo from './schoolLogo.png';
import logo from './sunLogo.png';
import { Button } from 'semantic-ui-react';
import { Line, Bar } from 'react-chartjs-2';
import { Chart, CategoryScale, LinearScale, PointElement, LineElement, BarElement, BarController, Title, Tooltip, Legend } from 'chart.js';

Chart.register(CategoryScale, LinearScale, PointElement, LineElement, BarElement, BarController, Title, Tooltip, Legend);

function ManageDevices() {
  const navigate = useNavigate();

  const goBack = () => {
    navigate(-1);
  };

  const data = {
    labels: ['1일', '2일', '3일', '4일', '5일', '6일', '7일'],
    datasets: [
      {
        label: '일주일 발전량 (직선)',
        data: [200, 900, 300, 500, 200, 300, 700],
        fill: false,
        backgroundColor: 'rgb(75, 192, 192)',
        borderColor: 'rgba(75, 192, 192, 0.2)',
      },
    ],
  };

  const options = {
    scales: {
      y: {
        ticks: {
          callback: function (value, index, values) {
            return value + ' mAh'; // y축의 값에 'mAh' 단위 추가
          },
        },
      },
    },
  };

  const [selectedGraph, setSelectedGraph] = useState('total'); // 현재 선택된 그래프를 추적하는 상태 변수

  // 일주일 동안의 발전량 합산
  const totalData = data.datasets[0].data.reduce((acc, curr) => acc + curr, 0);
  
  // iPhone 15를 충전할 수 있는 횟수 계산
  const forIphone15 = (totalData / 3877).toFixed(2); // 소수점 두 자리까지 표시

  const totalDataGraph = (
    <Bar 
      data={{ 
        labels: ['총 발전량'], 
        datasets: [{ 
          label: '총 발전량 (mAh)',
          data: [totalData], 
          backgroundColor: ['rgb(75, 192, 192)'] 
        }] 
      }} 
      options={options}
    />
  );

  const forIphone15Graph = (
    <Bar 
      data={{ 
        labels: ['충전 횟수 (아이폰 15 기준)'],
        datasets: [{ 
          label: '충전 횟수',
          data: [forIphone15], 
          backgroundColor: ['rgb(255, 99, 132)'] 
        }] 
      }} 
      options={options}
    />
  );

  const summaryData = {
    labels: ['총 발전량 (mAh)', '충전 횟수 (아이폰 15 기준)'],
    datasets: [
      {
        label: '총 발전량 (mAh)',
        data: [totalData, null],
        backgroundColor: ['rgb(75, 192, 192)'],
        yAxisID: 'y1',
      },
      {
        label: '충전 횟수 (아이폰 15 기준)',
        data: [null,forIphone15],
        backgroundColor: ['rgb(255,99,132)'],
        yAxisID: 'y2',
      }
    ],
  };

  const summaryOptions = {
    scales: {
      y1: {
        type: 'linear',
        position: 'left',
        ticks: {
          callback: function (value, index, values) {
            return value + ' mAh'; // y축의 값에 'mAh' 단위 추가
          },
        },
      },
      y2: {
        type: 'linear',
        position: 'right',
        grid: {
          drawOnChartArea: false, // 오른쪽 y축 그리드 라인을 그래프 영역에 그리지 않음
        },
        ticks: {
          callback: function (value, index, values) {
            return value + ' 회'; // y축의 값에 '회' 단위 추가
          },
        },
      },
    },
  };
  
  const summaryGraph = (
    <Bar data={summaryData} options={summaryOptions} />
  );

  return (
    <div className='App'>
      <div className='green-nav'>
        <h1>Green Solar System</h1>
      </div>
      <div style={{display: 'flex',justifyContent:'left'}}>
        <button className='backspace' onClick={goBack}>
          <img src={backspace} alt='Go back'style={{width:'20px',height:'20px'}}/>
        </button>
      </div>
      <div>
        <h1 style={{marginBottom:'20px'}}></h1>
        <Link to = "/"style={{display:'inline-block'}}>
          <img src={logo} className='App-logo'alt='logo'/>
        </Link>
        <h1 style={{ marginBottom:'20px'}}></h1>
        <div>
          <h3>발전량 통계</h3>
          <div style={{display: 'flex',justifyContent:'center',alignItems:'center',width:'100%',height:'300px'}}>
            <Line type = "line" data = {data} options={options} />
          </div>
        </div>
        <div style={{marginTop:'20px',textAlign:'center'}}>
          최근 일주일간 충전한 용량은 {totalData} mAh 이며, 이는 휴대폰을 {forIphone15}번 충전할 수 있습니다. (아이폰 15 기준)
        </div>
        <div style={{ display:'flex', justifyContent:'center', margin:'20px 0'}}>
          <Button onClick={() => setSelectedGraph('all')}>전체</Button>
          <Button onClick={() => setSelectedGraph('total')}>총 발전량</Button>
          <Button onClick={() => setSelectedGraph('iphone')}>환산값</Button>
        </div>
        <div style={{ display:'flex', justifyContent:'center', alignItems: 'center', width:'100%', height:'300px'}}>
          {selectedGraph === 'total' ? totalDataGraph : selectedGraph === 'iphone' ? forIphone15Graph : summaryGraph}
        </div>
        <div>
          <h1 style={{marginBottom:'10px'}}></h1>
        </div>
        <div>
          <Button primary>기간별 통계치</Button>
        </div>
        <div>
          <h1 style={{marginBottom:'50px'}}></h1>
        </div>
        <div className='gray-font'>
          <h4>로고 이미지를 클릭하면 홈 화면으로 이동합니다.</h4>
        </div>
        <div>
          <h1 style={{marginBottom:'20px'}}></h1>
        </div>
        <img src={schoolLogo} className='school-logo' alt='school logo' />
      </div>
    </div>
  );
}

export default ManageDevices;