import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import App from './App';
import ManageDevices from './ManageDevices';
import DeviceInfo from './DeviceInfo'; // Import DeviceInfo component
import RegisterDevice from './RegisterDevice'; // Import RegisterDevice component
import PowerStats from './PowerStats';

function Root() {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<App />} />
        <Route path="/manage-devices" element={<ManageDevices />} />
        <Route path="/power-stats" element={<PowerStats />} /> 
        <Route path="/device-info" element={<DeviceInfo />} /> // Add route for /device-info
        <Route path="/register-device" element={<RegisterDevice />} /> // Add route for /register-device
      </Routes>
    </Router>
  );
}

export default Root;

